.types {
  background-color: $light-grey;
  padding-bottom: 144px;
  padding-top: 149px;
  overflow: hidden;

  @include tablet {
    padding-top: 50px;
    padding-bottom: 70px;
  }

  @include mobile {
    padding-bottom: 52px;
  }
}

.types h2 {
  margin: 0;
  padding: 0;

  @include title;
  text-align: center;
}

.types h3 {
  margin: 0;
  margin-top: 20px;
  padding: 0;
  font-family: "OpenSans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 22px;
  line-height: 34px;
  text-align: center;
  letter-spacing: -0.5px;
  color: $special-black;

  @include mobile {
    margin-top: 20px;
  }
}

.types__list {
  margin: 0;
  margin-top: 60px;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  row-gap: 40px;

  @include tablet {
    row-gap: 50px;
    column-gap: 40px;
    margin-top: 30px;
    justify-content: center;
  }

  @include mobile {
    display: block;
  }
}

.types__item {
  max-width: 341px;
  width: 30%;

  &:nth-child(3n+2) {
    margin-top: 30px;
  }

  &:nth-child(3n) {
    margin-top: -10px;
  }

  @include tablet {
    // margin-bottom: 50px;
    // max-width: 50%;
    // width: 45%;
    min-width: 270px;

    &:nth-child(2n) {
      margin-top: 0;
    }

    &:nth-child(3n) {
      // margin: 0 auto 0 auto;
      margin: 0;
    }
  }

  @include mobile {
    margin: 0 auto;
    max-width: 100%;

    &:not(:last-child) {
      margin-bottom: 43px;
    }
  }
}

.types__link {
  display: block;
  text-decoration: none;
  width: fit-content;

  @include tablet {
    margin: 0 auto;
  }

  &:hover {
    opacity: 0.9;

    h3 {
      opacity: 0.7;
    }
  }

  &:active {
    opacity: 1;

    h3 {
      opacity: 1;
      color: $green;
    }
  }

  position: relative;
  z-index: 1;

  &::before {
    position: absolute;
    content: "";
    background-color: transparent;
  }

  &::after {
    position: absolute;
    content: "";
    background-color: transparent;
  }
}

.types__link img {
  object-fit: cover;
  border-radius: 10px;

  @include tablet {
    display: block;
    margin: 0 auto;
    width: 270px;
    height: 300px;
  }
}

.types__item:nth-child(3n+1) .types__link {
  &::before {
    z-index: -1;
    top: 157px;
    left: -60px;
    width: 180px;
    height: 180px;
    border: 27px solid $green;
    border-radius: 50%;

    @include tablet {
      top: 201px;
      left: -37px;
      width: 127px;
      height: 127px;
      border: 18px solid $green;
    }

    @include mobile {
      left: -24px;
    }
  }
}

.types__item:nth-child(3n+2) .types__link {
  &::before {
    z-index: 1;
    top: -18px;
    left: -19px;
    width: 47px;
    height: 47px;
    border: 8px solid $green;
  }

  &::after {
    z-index: 1;
    bottom: 35px;
    right: -23px;
    width: 47px;
    height: 47px;
    border: 8px solid $green;
  }

  @include tablet {
    &::before,
    &::after {
      display: none;
    }
  }
}

.types__item:nth-child(3n) .types__link {
  &::before {
    z-index: -1;
    top: -32px;
    right: -36px;
    width: 95px;
    height: 95px;
    border: 17px solid $green;
    border-radius: 50%;

    @include tablet {
      z-index: 1;
      top: -20px;
      right: -25px;
      width: 68px;
      height: 68px;
      border: 13px solid $green;
    }

    @include mobile {
      right: -15px;
    }
  }
}

